@media (max-width: 992px) {
  .product_name_blok {
    flex-direction: column;
    padding-top: 42px;
    text-align: center; }
  .video_blok {
    margin-top: 20px;
    margin-left: auto; } }

@media (max-width: 568px) {
  .product_name {
    padding: 16px 15px 10px 15px; }
  #menu__toggle {
    opacity: 0; }
  .menu__box {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none; }
    .menu__box .under {
      margin: 0;
      border-bottom: none;
      color: black; }
    .menu__box .menu__item {
      text-decoration: none;
      color: black;
      font-size: 24px;
      margin-left: 0px; }
      .menu__box .menu__item:hover {
        color: #fff; }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg); }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0); }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg); }
  #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    left: 0; }
  .menu__btn {
    display: flex;
    align-items: center;
    position: fixed;
    top: 20px;
    left: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1; }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition-duration: .25s; }
  .menu__btn > span::before {
    content: '';
    top: -8px; }
  .menu__btn > span::after {
    content: '';
    top: 8px; }
  .menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: 100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    background-color: #ECEFF1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
    transition-duration: .25s; }
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #445162;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: .25s; }
  .menu__item:hover {
    background-color: #CFD8DC; } }

